import * as moment from 'moment';
// import { EVENT_COLOR } from '../const/event-color.const';

export class Calendar {
  id!: number;
  content!: string;
  start!: string;
  type!: string;
  calendarType!: CalendarType;
}

export class CalendarEvent {
  id!: string;
  title!: string;
  start!: string;
  // end!: string;
  color!: string;
  allDay!: boolean;


  originData!: Calendar;

  constructor(data?: Calendar, i?: number, calendarType?: CalendarType) {
    if (data) {
      this.id = i.toString();
      this.title = data.type.toString();
      this.start = moment(new Date(data.start)).format('yyyy-MM-DD');
      // this.end = data.start;
      this.color = this.getColor(calendarType);
      this.originData = data;
      // this.allDay = true;
    }
  }
  private getColor(calendarType: CalendarType): string {
    let color = '#000000';
    switch (calendarType) {
      case CalendarType.fund:
        color = EVENT_COLOR.FUND;
        break;

      case CalendarType.invest:
        color = EVENT_COLOR.INVEST;
        break;
      case CalendarType.holiday:
        color = EVENT_COLOR.HOLIDAY;
        break;
      default:
        break;
    }
    return color;
  }
}

/** TODO: 我只有拿到中文，只能比較中文 */
export enum CalendarType {
  /** 基金配息基準日 */
  fund,
  /** 投資帳戶資產撥回基準日 */
  invest,
  /** 各國放假日 */
  holiday,
}

export const EVENT_COLOR = {
  /** 基金配息基準日  */
  FUND: '#213938',
  /** 投資帳戶資產撥回基準日  */
  INVEST: '#c4ac8c',
  /** 各國放假日  */
  HOLIDAY: '#BF4E58',
};
