import { Component, OnInit, ViewChild } from '@angular/core';
import { MercCalendarComponent } from 'merc-lib';
import { CalendarOptions } from '@fullcalendar/angular';
import { CsvData } from './models/csv-data.model';
import { HttpClient } from '@angular/common/http';
import { Calendar, CalendarEvent, CalendarType } from './models/calendars.model';
import { DatesSetArg, EventClickArg } from '@fullcalendar/angular'; // is important!
import * as moment from 'moment';
import { EventInput } from '@fullcalendar/core'; // is important!
import { AlertMessageModalComponent } from './modal/alert-message-modal/alert-message-modal.component';
import { NzModalService } from 'ng-zorro-antd/modal';

@Component({
  selector: 'app-calendar',
  templateUrl: './calendar.component.html',
  styleUrls: ['./calendar.component.scss']
})
export class CalendarComponent implements OnInit {
  /**
   * 月曆外觀設置及事件觸發設定
   * @references https://fullcalendar.io/docs/angular
   */
  calendarOptions: CalendarOptions = {
    navLinks: false,
    headerToolbar: {
      left: 'prev',
      center: 'title',
      right: 'next'
    },
  };
  /** 西元年 */
  @ViewChild('ceCalendar', { static: true }) ceCalendar: MercCalendarComponent;
  constructor(
    private httpClient: HttpClient,
    private modalService: NzModalService,
  ) { }

  ngOnInit(): void {
    this.ceCalendar.calendarOptions.headerToolbar = this.calendarOptions.headerToolbar;
    // 事件點擊事件
    this.ceCalendar.calendarOptions.eventClick = this.eventClick.bind(this);
    // 月曆開始渲染的時候
    this.ceCalendar.calendarOptions.datesSet = this.datesRender.bind(this);


  }

  /** 事件點擊 */
  eventClick(arg: EventClickArg): void {
    console.log(arg.event.title, ': ', arg.event.extendedProps);
    const modal = this.modalService.create({
      nzContent: AlertMessageModalComponent,
      nzWidth: 600,
      nzClosable: true,
      nzFooter: null,
      nzClassName: 'white-close-x',
      nzBodyStyle: {
        padding: '0',
      },
      nzComponentParams: {
        event: arg.event.extendedProps
      }
    });
  }

  /**
   * 月曆開始渲染的時候
   * @param arg DatesSetArg
   */
  datesRender(arg: DatesSetArg): void {
    console.log('datesRender');
    this.ceCalendar.calendarOptions.events = [];
    this.getCsv('./images/csv/Fund-20220210.csv', CalendarType.fund);
    this.getCsv('./images/csv/Holiday-20220127.csv', CalendarType.holiday);
    this.getCsv('./images/csv/Invest-0128.csv', CalendarType.invest);
  }

  /** 取得靜態資源 */
  getCsv(path: string, calendarType: CalendarType): void {
    this.httpClient.get(path, { responseType: 'text' })
      .subscribe(
        data => {
          this.csv2Json(data, calendarType);
        },
        err => {
          console.error(err);
        });
  }

  /** csv轉成Json */
  csv2Json(csv, calendarType: CalendarType): void {
    const papa = require('papaparse');
    papa.parse(csv, {
      // worker: true, // Don't bog down the main thread if its a big file
      header: true,
      transformHeader: (h, i) => {
        // TODO: HardCode
        if (h === '類型') {
          return h = 'type';
        }
        if (h === '日期') {
          return h = 'start';
        }
        if (h === '內容') {
          return h = 'content';
        }
        return h.trim();
      },
      // step: (result) => {
      //   // do stuff with result
      //   console.log(result);

      // },
      complete: (results, file) => {
        this.json2CalendarObject(results, calendarType);

      }
    });
  }

  /** json轉成日期物件 */
  json2CalendarObject(json: { data: any[], errors: any[], meta: any[] }, calendarType: CalendarType): void {
    const data: Calendar[] = json.data;
    if (data) {
      // this.ceCalendar.calendarOptions.events =
      //   data.map((event, index) => new CalendarEvent(event, index));
      const calendarEvent = data.map((event, index) => new CalendarEvent(event, index, calendarType));
      this.concatCalendarData(calendarEvent);
    }
  }

  concatCalendarData(calendarEvent: CalendarEvent[]): void {
    this.ceCalendar.calendarOptions.events = [...(this.ceCalendar.calendarOptions.events as any[]), ...calendarEvent];
  }

}

